.fileview{
    max-width:100%;
    display:flex;
    justify-content:flex-start;
    align-items:flex-start;
    padding:3em;
    border-radius:1em;
    color:var(--textColor);
    background-color:rgb(0,0,0,0.35);
    overflow:auto !important;
    --lineHeight:4.5em;
}
.fileview::-webkit-scrollbar{
    width:0.6em;
    height:0.6em;
}
.fileview::-webkit-scrollbar-thumb{
    border-radius:1em;
    background-color:var(--minorColor);
}
.fileview::-webkit-scrollbar-track{
    background-color:transparent;
}
.linecount{
    display:flex;
    flex-direction:column;
    justify-content:flex-start;
    align-items:flex-start;
    margin-right:1.5em !important;
}
.linecount>*{
    font-size:2em;
    display:flex;
    justify-content:center;
    align-items:center;
    height:calc(var(--lineHeight) / 2);
}
.linecount>*:first-child{
    margin-top:0;
}
.content{
    flex:1;
    font-family:Consolas,"Courier New", monospace;
    font-size:3em;
    line-height:calc(var(--lineHeight) / 3);
    white-space:pre;
    overflow:visible;
}

:root[theme=light]{
    .fileview{
        color:var(--backgroundColor);
        background-color:rgb(0,0,0,0.85);
    }
}

@media(any-hover){
    .content:hover{
        cursor:text;
    }
}
