.filenavigator{
    max-width:100%;
    display:flex;
    justify-content:space-between;
    align-items:stretch;
    flex-wrap:nowrap;
}
.tabnavigator{
    max-width:100%;
    height:unset;
    flex:1 1 0;
    border-radius:1em;
    overflow:hidden;
}
.header{
    flex-shrink:0;
    background-color:rgba(0,0,0,0.15);
}
.header::-webkit-scrollbar{
    display:none;
}
.header>*{
    flex-shrink:0 !important;
}
.header *{
    text-transform:none !important;
}
.container{
    overflow:hidden !important;
}
.fileview{
    max-height:81em;
    border-top-left-radius:0 !important;
    border-top-right-radius:0 !important;
}
.preview{
    max-width:100%;
    align-self:center;
}
div.preview{
    padding:5em !important;
    overflow:hidden;
}
img.preview{
    width:50em;
    margin:5em !important;
    border-radius:1em;
}

@media(max-width:15cm){
    .filenavigator{
        flex-direction:column;
    }
    .preview{
        padding:0 !important;
        padding-top:5em !important;
        padding-bottom:5em !important;
    }
    img.preview{
        width:65em;
    }
}

:root[theme=light]{
    .header{
        background-color:rgba(0,0,0,0.75);
    }
}
